import React, { Component } from 'react'
import {Grid} from 'antd-mobile'

export default class AvatarSelector extends Component {
    constructor(props){
        super(props)
        this.state={
            avatar:''
        }
    }
    render() {
        // 罗列出图片的数组
        const avatorList = 'boy,girl,man,woman,bull,chick,crab,hedgehog,hippopotamus,koala,lemur,pig,tiger,whale,zebra'
                            .split(',').map(v=>({
                                icon:require(`../../images/${v}.png`),
                                title:v
                            }))
        return (
            <div>
                 <div style={{margin:15,alignItems:'center',justifyContent:'center',flexDirection:'row'}}>
                     您选择的头像是：
                     <img src={this.state.avatar} alt=''/>
                 </div>
                 <Grid data={avatorList} columnNum={5} onClick={ele=>{
                     this.setState({
                        avatar:ele.icon 
                     })
                 }}/>
                 <div style={{textAlign:"center",margin:10}}>职位信息</div>
            </div>
        )
    }
}
